
@import 'styles/utils/vars.scss'; // for width vars
.time-selector {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	&__content-wrapper{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	&__sector {
		-webkit-user-select: none; /* Safari */        
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* IE10+/Edge */
		user-select: none; /* Standard */
		cursor: pointer;
		padding: 10px 10px;
		width: 120px;
		text-align: center;
		border: 1px solid #FFFFFF;
		border-radius: 8px;
		margin: 7px;

		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 16px;

		@media screen and (max-width:$smDesktopWidth) {
			width: 100px;
			font-size: 12px;
		}
		@media screen and (max-width:1052px) {
			width: 86px;
			font-size: 11px;
			padding: 7px 0;
			margin: 4px;
		}

		&.selected{
			background: var(--roomColor);
		}
		&.highlighted{
			filter: drop-shadow(0px 0px 10px var(--roomColor));
			// background: darken(var(--roomColor), 10%);
		}
		&.unavailable{
			opacity: 0.8;
			cursor: default;
		}
		&.booked{
			opacity: 1;
			background: #979797;
			cursor: default;;
		}

	}
}

