
	@import 'styles/utils/vars.scss';
	.booking {
		&__top-bar{
			position: relative;

		}
		&__logo {
			margin: auto;
			width: 150px;
			&>img{
				width: 100%;
				height: auto;
			}
		}
		&__title-wrapper {
			position: absolute;
			bottom: 0;
			left: 0;
			@media screen and (max-width: $tabletWidth) {
				position: relative;
				font-size: 48px;
			}

			font-family: serif;
			font-family: 'Playfair Display';
			font-style: normal;
			font-weight: 700;
			font-size: 64px;
		}
		&__steps-wrapper {
		}
		
		&__steps {
			margin: 30px auto 0 auto;
			width: 350px;
			@media screen and (max-width: $tabletWidth) {
				width: 280px;
			}
			// margin: auto;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__step {
			position: relative;
			&.active{
				.booking__step-circle{
					background: var(--roomColor);
					border: 1px solid #FFFFFF;
					color: #FFFFFF;
					box-shadow: 4px 4px 15px rgba(255, 255, 255, 0.9);
				}
				.booking__step-text{
					color: #FFFFFF;
					text-shadow: 4px 4px 15px rgba(255, 255, 255, 0.9);
					
				}
			}
		}
		&__step-circle{
			width: 30px;
			height: 30px;
			background: #979797;
			color: #D9D9D9;
			border-radius: 30px;
			display: flex;
			align-items: center;
			justify-content: center;

			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 16px;

			box-shadow: 4px 4px 15px rgba(255, 255, 255, 0);
			transition: all 0.5s;
		}
		&__step-text{
			position: absolute;
			bottom: 100%;
			left: 0;
			right: 0;
			display: flex;
			justify-content: center;

			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 14px;

			color: #979797;

			box-shadow: 4px 4px 15px rgba(255, 255, 255, 0);
			transition: all 0.5s;

		}
		&__steps-line{
			height: 0;
			flex-grow: 1;
			border: 1px dashed #FFFFFF;
			z-index: -5;
			transition: all 0.5s;
			&.active{
				border: 1px dashed var(--roomColor);
				box-shadow: 4px 4px 15px rgba(255, 255, 255, 0.9);

			}
		}
	}

